@use '../abstracts/' as a;

body {
  font-family: "Poppins", sans-serif;
  color: map-get(a.$clr, dark);
}

h1 {
  @include a.typeSpace(h1);
}

h2 {
  @include a.typeSpace(h2);
}

h3 {
  @include a.typeSpace(h3);
}

h4 {
  @include a.typeSpace(h4);
}

.label {
  @include a.typeSpace(label);
}
